import {
	colorF,
	integerF,
	selectF,
	booleanF,
	numberF,
	stringF,
	fontF,
	degreeF,
	gradientF
} from "@front/components/danmakuFlow/utils/form";
import style from "@front/datas/styleOptions";
export default () => {
	const config = {
		verticalAlign: selectF("", style.verticalAlign(), "25%"),
		rotateZ: integerF("", "25%"),
		position: selectF("", style.position(), "100%"),
		left: {
			"ui:hidden": "{{parentFormData.position !== 'absolute'}}",
			...integerF("", "25%")
		},
		right: {
			"ui:hidden": "{{parentFormData.position !== 'absolute'}}",
			...integerF("", "25%")
		},
		top: {
			"ui:hidden": "{{parentFormData.position !== 'absolute'}}",
			...integerF("", "25%")
		},
		bottom: {
			"ui:hidden": "{{parentFormData.position !== 'absolute'}}",
			...integerF("", "25%")
		},
		font: fontF("", "25%"),
		fontSize: integerF("", "25%"),
		fontWeight: selectF("", style.fontWeight(), "25%"),
		specificColor: booleanF("", "25%"),
		color: {
			...colorF("", "25%"),
			"ui:hidden": "{{!parentFormData.specificColor}}"
		},
		fontStyle: selectF("", style.fontWeight(), "25%"),
		textDecoration: selectF("", style.textDecoration(), "25%"),
		textShadowWidth: numberF("", "25%"),
		textShadowColor: colorF("", "25%", false),
		paddingLeft: integerF("", "25%"),
		paddingRight: integerF("", "25%"),
		paddingTop: integerF("", "25%"),
		paddingBottom: integerF("", "25%"),
		marginLeft: integerF("", "25%"),
		marginRight: integerF("", "25%"),
		marginTop: integerF("", "25%"),
		marginBottom: integerF("", "25%"),
		specificBackgroundColor: booleanF("", "25%"),
		backgroundColor: {
			...gradientF("", "25%"),
			"ui:hidden": "{{!parentFormData.specificBackgroundColor}}"
		},
		borderRadius: integerF("", "25%"),
		borderWidth: integerF("", "25%"),
		specificBorderColor: booleanF("", "25%"),
		borderColor: {
			...colorF("", "25%"),
			"ui:hidden": "{{!parentFormData.specificBorderColor}}"
		}
	};
	return {
		type: "object",
		properties: {
			config: {
				type: "object",
				title: "",
				properties: {
					owner: booleanF("", "50%"),
					degree: booleanF("", "25%"),
					name: booleanF("", "25%"),
					preview: integerF("", "100%"),
					color: {
						...degreeF("", "100%"),
						description:
							""
					}
				}
			},
			style: {
				type: "object",
				title: "",
				properties: {
					verticalAlign: selectF(
						"",
						style.verticalAlign(),
						"25%"
					),
					rotateZ: integerF("", "25%"),
					position: selectF("", style.position(), "100%"),
					left: {
						"ui:hidden":
							"{{parentFormData.position !== 'absolute'}}",
						...integerF("", "25%")
					},
					right: {
						"ui:hidden":
							"{{parentFormData.position !== 'absolute'}}",
						...integerF("", "25%")
					},
					top: {
						"ui:hidden":
							"{{parentFormData.position !== 'absolute'}}",
						...integerF("", "25%")
					},
					bottom: {
						"ui:hidden":
							"{{parentFormData.position !== 'absolute'}}",
						...integerF("", "25%")
					},
					paddingLeft: integerF("", "25%"),
					paddingRight: integerF("", "25%"),
					paddingTop: integerF("", "25%"),
					paddingBottom: integerF("", "25%"),
					marginLeft: integerF("", "25%"),
					marginRight: integerF("", "25%"),
					marginTop: integerF("", "25%"),
					marginBottom: integerF("", "25%"),
					specificBackgroundColor: booleanF("", "25%"),
					backgroundColor: {
						...gradientF("", "25%"),
						"ui:hidden":
							"{{!parentFormData.specificBackgroundColor}}"
					},
					borderRadius: integerF("", "25%"),
					borderWidth: integerF("", "25%"),
					specificBorderColor: booleanF("", "25%"),
					borderColor: {
						...colorF("", "25%"),
						"ui:hidden": "{{!parentFormData.specificBorderColor}}"
					}
				}
			},
			degreeStyle: {
				type: "object",
				title: "",
				"ui:hidden": "{{!rootFormData.config.degree}}",
				properties: config
			},
			nameStyle: {
				type: "object",
				title: "",
				"ui:hidden": "{{!rootFormData.config.name}}",
				properties: config
			}
		}
	};
};
