import {
	colorF,
	integerF,
	selectF,
	numberF,
	stringF,
	booleanF,
	gradientF,
	fontF
} from "@front/components/danmakuFlow/utils/form";
import style from "@front/datas/styleOptions";

const config = () => {
	return {
		type: "object",
		title: "",
		properties: {
			verticalAlign: selectF("", style.verticalAlign(), "25%"),
			rotateZ: integerF("", "25%"),
			position: selectF("", style.position(), "100%"),
			left: {
				"ui:hidden": "{{parentFormData.position !== 'absolute'}}",
				...integerF("", "25%")
			},
			right: {
				"ui:hidden": "{{parentFormData.position !== 'absolute'}}",
				...integerF("", "25%")
			},
			top: {
				"ui:hidden": "{{parentFormData.position !== 'absolute'}}",
				...integerF("", "25%")
			},
			bottom: {
				"ui:hidden": "{{parentFormData.position !== 'absolute'}}",
				...integerF("", "25%")
			},

			font: fontF("", "25%"),
			fontSize: integerF("", "25%"),
			fontWeight: selectF("", style.fontWeight(), "25%"),
			color: colorF("", "25%"),
			fontStyle: selectF("", style.fontWeight(), "25%"),
			textDecoration: selectF("", style.textDecoration(), "25%"),
			textShadowWidth: numberF("", "25%"),
			textShadowColor: colorF("", "25%", false),
			paddingLeft: integerF("", "25%"),
			paddingRight: integerF("", "25%"),
			paddingTop: integerF("", "25%"),
			paddingBottom: integerF("", "25%"),
			marginLeft: integerF("", "25%"),
			marginRight: integerF("", "25%"),
			marginTop: integerF("", "25%"),
			marginBottom: integerF("", "25%"),
			backgroundColor: gradientF("", "25%"),
			borderRadius: integerF("", "25%"),
			borderWidth: integerF("", "25%"),
			borderColor: colorF("", "25%")
		}
	};
};
export default () => {
	return {
		type: "object",
		properties: {
			config: {
				type: "object",
				title: "",
				properties: {
					preview: selectF(
						"",
						[
							{
								label: "",
								value: "owner"
							},
							{
								label: "",
								value: "manager"
							}
						],
						"100%"
					),
					owner: {
						"ui:hidden": "{{parentFormData.preview!=='owner'}}",
						...booleanF("")
					},
					ownerTitle: {
						"ui:hidden": "{{parentFormData.preview!=='owner'}}",
						...stringF("")
					},
					manager: {
						"ui:hidden": "{{parentFormData.preview!=='manager'}}",
						...booleanF("")
					},
					managerTitle: {
						"ui:hidden": "{{parentFormData.preview!=='manager'}}",
						...stringF("")
					}
				}
			},
			ownerStyle: {
				"ui:hidden": "{{parentFormData.config.preview!=='owner'}}",
				...config()
			},
			managerStyle: {
				"ui:hidden": "{{parentFormData.config.preview!=='manager'}}",
				...config()
			}
		}
	};
};
