import {
	colorF,
	integerF,
	selectF,
	booleanF,
	numberF,
	fontF,
	gradientF
} from "@front/components/danmakuFlow/utils/form";
import style from "@front/datas/styleOptions";
import { giftTypeOptions } from "@front/components/danmakuFlow/utils/data";
export default () => {
	const noInPicture = [
		"fontSize",
		"fontWeight",
		"color",
		"fontStyle",
		"textDecoration",
		"textShadowWidth",
		"textShadowColor",
		"paddingLeft",
		"paddingRight",
		"paddingTop",
		"paddingBottom"
	];
	const form = {
		type: "object",
		properties: {
			config: {
				type: "object",
				title: "",
				properties: {
					type: selectF("", giftTypeOptions(), "50%")
				}
			},
			style: {
				type: "object",
				title: "",
				properties: {
					width: {
						...integerF("", "25%"),
						"ui:hidden":
							"{{rootFormData.config.type !== 'picture'}}"
					},
					height: {
						...integerF("", "25%"),
						"ui:hidden":
							"{{rootFormData.config.type !== 'picture'}}"
					},
					verticalAlign: selectF(
						"",
						style.verticalAlign(),
						"25%"
					),
					rotateZ: integerF("", "25%"),

					position: selectF("", style.position(), "25%"),
					zIndex: integerF("", "25%"),
					left: {
						"ui:hidden":
							"{{parentFormData.position !== 'absolute'}}",
						...integerF("", "25%")
					},
					right: {
						"ui:hidden":
							"{{parentFormData.position !== 'absolute'}}",
						...integerF("", "25%")
					},
					top: {
						"ui:hidden":
							"{{parentFormData.position !== 'absolute'}}",
						...integerF("", "25%")
					},
					bottom: {
						"ui:hidden":
							"{{parentFormData.position !== 'absolute'}}",
						...integerF("", "25%")
					},

					font: fontF("", "25%"),
					fontSize: integerF("", "25%"),
					fontWeight: selectF("", style.fontWeight(), "25%"),
					color: colorF("", "25%"),
					fontStyle: selectF("", style.fontWeight(), "25%"),
					textDecoration: selectF(
						"",
						style.textDecoration(),
						"25%"
					),
					textShadowWidth: numberF("", "25%"),
					textShadowColor: colorF("", "25%", false),
					paddingLeft: integerF("", "25%"),
					paddingRight: integerF("", "25%"),
					paddingTop: integerF("", "25%"),
					paddingBottom: integerF("", "25%"),
					marginLeft: integerF("", "25%"),
					marginRight: integerF("", "25%"),
					marginTop: integerF("", "25%"),
					marginBottom: integerF("", "25%"),
					backgroundColor: gradientF("", "25%"),
					borderRadius: integerF("", "25%"),
					borderWidth: integerF("", "25%"),
					borderColor: colorF("", "25%")
				}
			}
		}
	};
	for (const style of noInPicture) {
		// @ts-ignore
		form.properties.style.properties[style]["ui:hidden"] =
			"{{rootFormData.config.type === 'picture'}}";
	}
	return form;
};
