import { defineAsyncComponent, markRaw } from "vue";
const requireComponent = require.context("@front/applets", true, /\w+\.(vue)$/);

const applets: any = [];

requireComponent.keys().forEach(fileName => {
	const componentConfig = requireComponent(fileName);
	applets.push({
		cname: componentConfig.default.cname || componentConfig.default.name,
		name: componentConfig.default.name,
		icon: componentConfig.default.icon || "el-icon-ice-cream-round",
		component: markRaw(
			defineAsyncComponent(() =>
				import(`@front/applets/${fileName.replace("./", "")}`)
			)
		),
		configurations: componentConfig.default.configurations || {}
	});
});

export default applets;
