import {
	colorF,
	integerF,
	selectF,
	numberF,
	stringF,
	gradientF,
	backgroundImageF,
	fontF
} from "@front/components/danmakuFlow/utils/form";
import style from "@front/datas/styleOptions";
export default () => {
	return {
		type: "object",
		properties: {
			config: {
				type: "object",
				title: "",
				properties: {
					text: stringF("", "25%")
				}
			},
			style: {
				type: "object",
				title: "",
				properties: {
					width: integerF("", "25%"),
					height: integerF("", "25%"),
					verticalAlign: selectF(
						"",
						style.verticalAlign(),
						"25%"
					),
					rotateZ: integerF("", "25%"),
					position: selectF("", style.position(), "25%"),
					zIndex: integerF("", "75%"),
					left: {
						"ui:hidden":
							"{{parentFormData.position !== 'absolute'}}",
						...integerF("", "25%")
					},
					right: {
						"ui:hidden":
							"{{parentFormData.position !== 'absolute'}}",
						...integerF("", "25%")
					},
					top: {
						"ui:hidden":
							"{{parentFormData.position !== 'absolute'}}",
						...integerF("", "25%")
					},
					bottom: {
						"ui:hidden":
							"{{parentFormData.position !== 'absolute'}}",
						...integerF("", "25%")
					},

					font: fontF("", "25%"),
					fontSize: integerF("", "25%"),
					fontWeight: selectF("", style.fontWeight(), "25%"),
					color: colorF("", "25%"),
					fontStyle: selectF("", style.fontWeight(), "25%"),
					textDecoration: selectF(
						"",
						style.textDecoration(),
						"25%"
					),
					textShadowWidth: numberF("", "25%"),
					textShadowColor: colorF("", "25%", false),
					paddingLeft: integerF("", "25%"),
					paddingRight: integerF("", "25%"),
					paddingTop: integerF("", "25%"),
					paddingBottom: integerF("", "25%"),
					marginLeft: integerF("", "25%"),
					marginRight: integerF("", "25%"),
					marginTop: integerF("", "25%"),
					marginBottom: integerF("", "25%"),
					backgroundColor: gradientF("", "25%"),
					backgroundImage: backgroundImageF(""),
					borderRadius: integerF("", "25%"),
					borderWidth: integerF("", "25%"),
					borderColor: colorF("", "25%")
				}
			}
		}
	};
};
